import React, { useState } from 'react';

const Link = ({ href, children, className }) => (
  <a href={href} className={className}>{children}</a>
);

export default function FuturisticHeader() {
  const [hoveredLink, setHoveredLink] = useState(null);

  return (
    <header className="relative w-full bg-gradient-to-r from-black via-purple-950 to-black text-white py-6 overflow-hidden">
      {/* Animated Background Stars */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(30)].map((_, i) => (
          <div
            key={i}
            className="absolute w-1 h-1 bg-white rounded-full animate-pulse"
            style={{
              top: `${Math.random() * 100}%`,
              left: `${Math.random() * 100}%`,
              animationDelay: `${Math.random() * 3}s`,
              opacity: Math.random() * 0.7 + 0.3,
            }}
          />
        ))}
      </div>

      {/* Scanning Line Effect */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute h-px w-full bg-gradient-to-r from-transparent via-cyan-400 to-transparent animate-scan" />
      </div>

      {/* Gradient Overlay */}
      <div className="absolute inset-0 bg-gradient-to-b from-blue-900/20 via-purple-900/20 to-black/20 pointer-events-none" />

      <nav className="relative max-w-7xl mx-auto px-6 flex items-center justify-between">
        {/* Logo/Brand Section */}
        <div className="flex items-center gap-3">
          <div className="relative w-10 h-10">
            <div className="absolute inset-0 bg-gradient-to-br from-cyan-400 to-purple-600 rounded-lg animate-pulse" />
            <div className="absolute inset-0.5 bg-black rounded-lg flex items-center justify-center">
              <div className="w-5 h-5 bg-gradient-to-br from-cyan-400 to-purple-600 rounded-sm animate-spin-slow" />
            </div>
          </div>
          <span className="text-2xl font-bold bg-gradient-to-r from-cyan-400 via-purple-400 to-pink-400 bg-clip-text text-transparent">
            NEXUS
          </span>
        </div>

        {/* Navigation Links - Right Aligned */}
        <div className="flex items-center gap-4">
          {[
            { href: '/', label: 'Home', icon: 'âŒ‚' },
            { href: '/worldPin', label: 'World Pin', icon: 'ðŸŒ' },
            { href: '/transaction', label: 'Transaction', icon: 'âš¡' }
          ].map((link, index) => (
            <Link
              key={link.href}
              href={link.href}
              className="group relative px-6 py-2.5 overflow-hidden"
              onMouseEnter={() => setHoveredLink(index)}
              onMouseLeave={() => setHoveredLink(null)}
            >
              {/* Background Glow */}
              <div className={`absolute inset-0 bg-gradient-to-r from-cyan-600/30 via-purple-600/30 to-pink-600/30 rounded-lg blur-md transition-opacity duration-300 ${
                hoveredLink === index ? 'opacity-100' : 'opacity-0'
              }`} />
              
              {/* Border Animation */}
              <div className="absolute inset-0 rounded-lg overflow-hidden">
                <div className={`absolute inset-0 border-2 border-transparent bg-gradient-to-r from-cyan-500 via-purple-500 to-pink-500 rounded-lg transition-all duration-300 ${
                  hoveredLink === index ? 'animate-border-flow' : ''
                }`} style={{
                  maskImage: 'linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0)',
                  WebkitMaskImage: 'linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0)',
                  maskComposite: 'exclude',
                  WebkitMaskComposite: 'xor'
                }} />
              </div>

              {/* Button Background */}
              <div className={`absolute inset-0 bg-gradient-to-br from-gray-900/90 to-black/90 rounded-lg backdrop-blur-sm transition-all duration-300 ${
                hoveredLink === index ? 'scale-105' : 'scale-100'
              }`} />

              {/* Content */}
              <div className="relative flex items-center gap-2 z-10">
                <span className="text-xl transition-transform duration-300 group-hover:scale-110">
                  {link.icon}
                </span>
                <span className={`font-semibold tracking-wide transition-all duration-300 ${
                  hoveredLink === index 
                    ? 'bg-gradient-to-r from-cyan-400 via-purple-400 to-pink-400 bg-clip-text text-transparent' 
                    : 'text-gray-300'
                }`}>
                  {link.label}
                </span>
              </div>

              {/* Hover Particles */}
              {hoveredLink === index && (
                <>
                  {[...Array(5)].map((_, i) => (
                    <div
                      key={i}
                      className="absolute w-1 h-1 bg-cyan-400 rounded-full animate-particle"
                      style={{
                        top: `${Math.random() * 100}%`,
                        left: `${Math.random() * 100}%`,
                        animationDelay: `${i * 0.1}s`,
                      }}
                    />
                  ))}
                </>
              )}
            </Link>
          ))}
        </div>
      </nav>

      <style jsx>{`
        @keyframes scan {
          0% {
            transform: translateY(-100%);
          }
          100% {
            transform: translateY(400%);
          }
        }

        @keyframes spin-slow {
          from {
            transform: rotate(0deg);
          }
          to {
            transform: rotate(360deg);
          }
        }

        @keyframes border-flow {
          0% {
            background-position: 0% 50%;
          }
          50% {
            background-position: 100% 50%;
          }
          100% {
            background-position: 0% 50%;
          }
        }

        @keyframes particle {
          0% {
            transform: translateY(0) scale(1);
            opacity: 1;
          }
          100% {
            transform: translateY(-20px) scale(0);
            opacity: 0;
          }
        }

        .animate-scan {
          animation: scan 4s linear infinite;
        }

        .animate-spin-slow {
          animation: spin-slow 3s linear infinite;
        }

        .animate-border-flow {
          background-size: 200% 200%;
          animation: border-flow 2s ease infinite;
        }

        .animate-particle {
          animation: particle 1s ease-out infinite;
        }
      `}</style>
    </header>
  );
}